body{
  margin:0;
}
ul{list-style: none; display: flex; text-align: center;}
li{margin:0px 10px 0px 0px;}
a{text-decoration: none; cursor: help;}

.movie{
      width: 250px;
      height: auto;
      column-width:250px;
      transition: all 2s;
    }
    .movie figure{
      display: inline-block;
      border:1px solid rgba(0,0,0,0.2);
      border-radius: 15px;
      margin:15px 0px 25px 15px;
      padding:10px;
      box-shadow: 2px 2px 5px rgba(0,0,0,0.5);;
      background-color: #d1d1d1;
      text-align: center;
      transition: all 2s;
    }
    .movie figure img{
      width:100%;
      border-radius: 15px;
      overflow: hidden;
    }
.movies-title , .movies-rating {border-bottom: 1.35px solid; padding:10px 0px 10px 0px; margin:0;}
.movies-title{width: 250px; height: 45px; font-size: 20px; font-family:inherit;}
.genres{margin:10px 0px 10px 0px; padding:0;  justify-content: center; font-weight: 500;}
.summary{display: none; border-bottom: 1px solid;margin:0; padding:10px 0px 10px 0px; width: 250px; height: 130px; overflow:auto;
}

.movie:hover span {
  transform: scale(1.1);
    transition: all 2s;
    padding: 5px 20px 5px 5px;
    display: inline-block;
    z-index: 100;
    margin: 10px;
    width: 220px;
    text-decoration: none;


}

